import React, { Component } from 'react';
import "./App.css";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import withMobileDialog from '@material-ui/core/withMobileDialog';
import Collapse from '@material-ui/core/Collapse';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import { Blog1, Blog1Code } from './blogs/canvas/blog1';

const styles = theme => ({
    list: {
        width: 250,
    },

    fab: {
        position: 'fixed',
        bottom: theme.spacing.unit * 2,
        right: theme.spacing.unit * 2,
        background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
        boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
    },

    App: {
        flexGrow: 1,
        padding: 20
    },

    paper: {
        padding: theme.spacing.unit * 2,
        textAlign: 'center',
        color: theme.palette.text.secondary,
        minHeight: 200
    },

    nested: {
        paddingLeft: theme.spacing.unit * 4,
    }

});



class App extends Component {
    state = {
        show: false,
        open: false,
        openlist: false,
    }

    toggleDrawer = (open) => () => {
        this.setState({
            "show": open,
        });
    };

    showBlog = () => {
        this.setState({ open: true });
    }

    closeBlog = () => {
        this.setState({ open: false });
    };

    handleClick = () => {
        this.setState(state => ({ openlist: !state.openlist }));
    };

    render() {

        const { classes, fullScreen } = this.props;

        const sideList = (
            <div className={classes.list} >
                <List>
                    <ListItem button onClick={this.handleClick}>
                        <ListItemIcon><InboxIcon /></ListItemIcon>
                        <ListItemText inset primary={"Canvas"} />
                        {this.state.openlist ? <ExpandLess /> : <ExpandMore />}
                    </ListItem>
                    <Collapse in={this.state.openlist} timeout="auto" unmountOnExit>
                        <List component="div" disablePadding>
                            <ListItem button className={classes.nested} onClick={this.toggleDrawer(false)}>
                                <ListItemText primary="canvas高级动画示例" />
                            </ListItem>
                        </List>
                    </Collapse>
                    {['1', '2', '3', '4'].map(text => (
                        <ListItem button key={text} onClick={this.toggleDrawer(false)}>
                            <ListItemIcon><InboxIcon /></ListItemIcon>
                            <ListItemText primary={text} />
                        </ListItem>

                    ))}
                </List>
                <Divider />
                <List>
                    {['5', '6', '7'].map(text => (
                        <ListItem button key={text}>
                            <ListItemIcon><InboxIcon /></ListItemIcon>
                            <ListItemText primary={text} />
                        </ListItem>
                    ))}
                </List>
            </div>
        );

        return (
            <div className={classes.App}>
                <Button className={classes.fab} variant="contained" color="primary" onClick={this.toggleDrawer(true)}>博客列表</Button>
                <Drawer anchor="right" open={this.state.show} onClose={this.toggleDrawer(false)}>
                    <div
                        tabIndex={0}
                        role="button"
                    >
                        {sideList}
                    </div>
                </Drawer>


                <Grid container spacing={40} >
                    <Grid item xs={12} sm={6} md={4} lg={3} xl={3}>
                        <Paper className={classes.paper + " " + "mypaper"} onClick={this.showBlog}>
                            <Typography variant="h5" component="h3">
                                canvas高级动画示例
                            </Typography>
                            <Typography component="p">
                                本文介绍如何使用canvas绘制高级动画
                            </Typography>
                        </Paper>
                    </Grid>
                    <Grid item xs={12} sm={6} md={4} lg={3} xl={3} onClick={this.showBlog}>
                        <Paper className={classes.paper + " " + "mypaper"}>xs=6</Paper>
                    </Grid>
                    <Grid item xs={12} sm={6} md={4} lg={3} xl={3} onClick={this.showBlog}>
                        <Paper className={classes.paper + " " + "mypaper"}>xs=6</Paper>
                    </Grid>
                    <Grid item xs={12} sm={6} md={4} lg={3} xl={3} onClick={this.showBlog}>
                        <Paper className={classes.paper + " " + "mypaper"}>xs=3</Paper>
                    </Grid>
                </Grid>

                <Dialog
                    fullScreen={true}
                    open={this.state.open}
                    onClose={this.closeBlog}
                    aria-labelledby="responsive-dialog-title"
                >
                    <DialogTitle id="responsive-dialog-title">{"canvas高级动画示例"}</DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            <Blog1/>
                            <Blog1Code/>
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={this.closeBlog} color="primary">
                            Disagree
                        </Button>
                        <Button onClick={this.closeBlog} color="primary" autoFocus>
                            Agree
                        </Button>
                    </DialogActions>
                </Dialog>
            </div>
        );
    }
}

App.propTypes = {
    classes: PropTypes.object.isRequired,
    theme: PropTypes.object.isRequired,
    fullScreen: PropTypes.bool.isRequired
};
export default withMobileDialog()(withStyles(styles, { withTheme: true })(App));
